body {
  min-width: 320px;
  margin: 0;
  overflow: overlay;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  background-color: #f9fbfd; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 30px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 45px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; } }

header {
  height: auto;
  min-height: 650px;
  background-color: #003c76;
  background-image: url("../img/main-banner-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    header {
      background-image: url("../img/main-banner-desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    header {
      min-height: 638px;
      background-image: url("../img/main-banner-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    header {
      background-image: url("../img/main-banner-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    header {
      min-height: 500px; } }
  @media (max-width: 320px) {
    header {
      background-image: url("../img/main-banner-mobile@1x.jpg"); } }
  @media (max-width: 320px) and (min-resolution: 144dpi), (max-width: 320px) and (min-resolution: 1.5dppx) {
    header {
      background-image: url("../img/main-banner-mobile@2x.jpg"); } }

.header__top {
  margin-bottom: 192px;
  padding-top: 34px; }
  @media (max-width: 1023px) {
    .header__top {
      margin-bottom: 211px;
      padding-top: 25px; } }
  @media (max-width: 767px) {
    .header__top {
      margin-bottom: 130px;
      padding-top: 20px; } }

.header__title {
  padding-left: 125px; }
  @media (max-width: 1023px) {
    .header__title {
      padding-left: 45px; } }
  @media (max-width: 767px) {
    .header__title {
      padding-left: 15px; } }

.header__title h1 {
  margin: 0;
  margin-bottom: 13px;
  color: #f9fbfd;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px; }
  @media (max-width: 1023px) {
    .header__title h1 {
      font-weight: 700;
      font-size: 50px;
      line-height: 61px; } }
  @media (max-width: 767px) {
    .header__title h1 {
      margin-bottom: 11px;
      font-weight: 700;
      font-size: 36px;
      line-height: 45px; } }

.header__title p {
  margin: 0;
  color: #f9fbfd;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px; }
  @media (max-width: 1023px) {
    .header__title p {
      font-weight: 400;
      font-size: 18px;
      line-height: 22px; } }
  @media (max-width: 767px) {
    .header__title p {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px; } }

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 767px) {
    .header-top {
      position: relative;
      flex-direction: column;
      align-items: baseline; }
      .header-top--closed .header-top__list {
        display: none; }
      .header-top--closed .header-top__toggle {
        position: absolute;
        top: 22px;
        right: 15px;
        background-image: url("../img/icon-open.svg");
        fill: #f9fbfd; }
      .header-top--opened {
        margin-bottom: 5px;
        background-color: #f9fbfd; }
        .header-top--opened .header-top__logo-svg {
          fill: #011c40; }
        .header-top--opened .header-top__toggle {
          position: absolute;
          top: 23px;
          right: 10px;
          background-image: url("../img/icon-close.svg");
          fill: #011c40; } }

@media (max-width: 767px) {
  .header-top--nojs .header-top__toggle {
    display: none; } }

.header-top__logo-svg {
  width: 70px;
  height: 40px;
  fill: #f9fbfd; }
  @media (max-width: 1023px) {
    .header-top__logo-svg {
      width: 38px;
      height: 20px; } }
  @media (max-width: 767px) {
    .header-top__logo-svg {
      width: 42px;
      height: 22px; } }

.main-nav {
  width: 80%; }

.main-nav__toggle {
  display: none; }
  @media (max-width: 767px) {
    .main-nav__toggle {
      display: block;
      width: 24px;
      height: 17px;
      background-color: transparent;
      background-repeat: no-repeat;
      background-position: 50%;
      border: 0; } }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (max-width: 767px) {
    .main-nav__list {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: 100%;
      min-height: 160px;
      padding: 31px 0 50px; } }

.main-nav__item {
  padding: 7px 0 7px 34px; }
  @media (max-width: 1023px) {
    .main-nav__item {
      padding-left: 24px; } }
  @media (max-width: 767px) {
    .main-nav__item {
      padding: 11px 0 10px; }
      .main-nav__item--destinations {
        order: -1; } }

.main-nav__link {
  color: #f9fbfd;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px; }
  @media (max-width: 1023px) {
    .main-nav__link {
      text-align: right;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px; } }
  @media (max-width: 767px) {
    .main-nav__link {
      color: #011c40;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px; } }

.intro {
  display: flex;
  justify-content: space-between;
  min-height: 541px;
  padding-top: 120px;
  padding-bottom: 120px; }
  @media (max-width: 1023px) {
    .intro {
      min-height: 464px;
      padding-top: 91px; } }
  @media (max-width: 767px) {
    .intro {
      flex-wrap: wrap;
      padding-top: 57px;
      padding-bottom: 38px; } }

.intro__text {
  width: 50%;
  padding-top: 55px; }
  @media (max-width: 1023px) {
    .intro__text {
      padding-top: 0; } }
  @media (max-width: 767px) {
    .intro__text {
      width: 100%;
      margin-bottom: 32px; } }

.intro__image {
  width: 560px;
  background-image: url("../img/penguins-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .intro__image {
      background-image: url("../img/penguins-desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    .intro__image {
      width: 324px;
      background-image: url("../img/penguins-tablet@1x.jpg");
      background-repeat: no-repeat;
      background-position: right;
      background-size: 100%; } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .intro__image {
      background-image: url("../img/penguins-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .intro__image {
      width: 100%;
      min-height: 186px;
      background-image: url("../img/penguins-mobile@1x.jpg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 290px 186px; } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .intro__image {
      background-image: url("../img/penguins-mobile@2x.jpg"); } }

.intro h2 {
  margin: 0;
  margin-bottom: 25px;
  color: #011c40;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px; }
  @media (max-width: 1023px) {
    .intro h2 {
      margin-bottom: 16px;
      padding-right: 37px;
      font-weight: 600;
      font-size: 26px;
      line-height: 36px; } }
  @media (max-width: 767px) {
    .intro h2 {
      font-weight: 600;
      font-size: 24px;
      line-height: 34px; } }

.intro p {
  margin: 0;
  padding-right: 45px;
  color: #011c40;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px; }
  @media (max-width: 1023px) {
    .intro p {
